
    import { defineComponent } from 'vue';
    import moment from 'moment';

    export default defineComponent ({
        name: 'DataTitle',
        props: {
            'text': String,
            dataDate: String,
        },
        computed: {
            timestamp: function() {
                return moment(this.dataDate).format('MMMM Do YYYY, h:mm:ss a')
            }
        }
    })
