
    import { defineComponent } from 'vue';
    
    export default defineComponent ({
        name: 'CountrySelect',
        props: ['countries'],
        data() {
            return {
                selected: 0,
            }
        },
        methods: {
            onChange() {
                const country = this.countries.find((item: any) => item.ID === this.selected)
                this.$emit('get-country', country)
            }
        }
    })


